.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.square{
  height:80px;
  width:80px;
  border:2px solid black;
  font-size: 60px;
  text-align: center;
}
#board{
min-height: 70%;
min-width:100%;
display:flex;
align-items: center;
justify-content: center;
}
#status{
font-size: 1.5rem;
text-align: center;
}
#title{
  text-align: center;
  font-size: 5rem;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
